import React, { useEffect, useState } from 'react';
import Header from '../useradmin/header';
import ModalBox from './ModalBox'
import { Router, withRouter } from 'react-router-dom'
import { Button, Row, Col } from 'react-bootstrap'
import axios from 'axios';
var dateFormat = require('dateformat');

const BookingHistoryPage = (props) => {
    const [tableData, setData] = useState([])
    const [orderInfo, setOrderInfo] = useState(null)
    const [showInfo, setShowInfo] = useState(false)
    const loadData = async () => {
        const result = await axios.get(global.serverURL + `/api/user/booking_history?mobno=${global.mobno}`)
        setData(result?.data)
    }

    useEffect(() => {
        loadData()
    }, [])

    const handleFavourite = async (item) => {
        const { BookingId, isFavourite } = item
        const result = await axios.post(global.serverURL + `/api/user/booking_history`,
            { bid: BookingId, isFavourite: !isFavourite })
        if (result) {
            loadData()
        }
    }

    const handleOrderInfo = (item) => {
        setShowInfo(true)
        setOrderInfo(item)
    }

    const handleClose = () => {
        setShowInfo(false)
    }

    const OrderInfo = () => {
        if (!orderInfo) return null;
        const orderData = {
            'Booking Id': orderInfo.BookingId,
            'From address': orderInfo.FromAddress,
            'To address': orderInfo.toaddress,
            'Receiver name': orderInfo.RecName,
            'Receiver address': orderInfo.LocalAdd1 + ',' + orderInfo.LocalAdd2,
            'Receiver contacts': orderInfo.RecMobile,
            'Product type': orderInfo.ProductType,
            'No. of Items': orderInfo.noi,
            'Item description & consignment value': orderInfo.Remarks,
            'Payment mode': orderInfo.PaymentMode,
            'Net total': orderInfo.NetTotal,
        }
        return (
            <ModalBox
                isShow={showInfo}
                title="Booking Information"
                handleCancel={handleClose}
            >
                <>
                    {Object.entries(orderData).map(([key, value], idx) => {
                        return (<>
                            <Row key={idx}>
                                <Col>{key}:</Col>
                                <Col>{value}</Col>
                            </Row>
                            <hr />
                        </>
                        )
                    })}
                </>
            </ModalBox>
        )
    }

    const editOrder = (item) => {
        localStorage.setItem('_order_edit', JSON.stringify(item))
        props.history.push({
            pathname: '/mybooking',
            state: item
        })
    }

    const reBooking = (item) => {
        localStorage.setItem('_order_edit', JSON.stringify({ ...item, isRebooking: true }))
        props.history.push({
            pathname: '/mybooking',
            state: item
        })
    }

    const TableData = () => {
        return tableData.length ? tableData?.map((item, key) => {
            const booktime = dateFormat(item?.BookDate, "dddd, mmm dS, yyyy ", true) + item?.BookingTime;
            return (
                <tr key={key}>
                    <td data-th="favourite" className='text-center'>
                        <i onClick={() => handleFavourite(item)}
                            className={`fa ${item?.isFavourite
                                ? 'fa-heart text-danger' : 'fa-heart-o'} fa-lg cursor-pointer`}
                            aria-hidden="true"></i>
                    </td>
                    <td data-th="Booking #">{item?.BookingId}</td>
                    <td data-th="Booking Time">{booktime}</td>
                    <td data-th="Payment Mode" className="text-capitalize">{item?.PaymentMode}</td>
                    <td data-th="Amount">{item?.NetTotal}</td>
                    <td data-th="Actions">
                        <div className='d-flex'>
                            <Button variant="primary" className='mx-2' onClick={() => reBooking(item)}>Re-Booking</Button>
                            <Button variant="danger" className='mx-2' onClick={() => editOrder(item)}>
                                Edit Order
                            </Button>
                            <Button variant="success" className='mx-2'
                                onClick={() => handleOrderInfo(item)}>Info</Button>
                        </div>
                    </td>
                </tr>
            )
        }) : null
    }

    const TableComponent = () => (
        <table className="rwd-table">
            <tbody>
                <tr>
                    <th></th>
                    <th>Booking #</th>
                    <th>Booking Time</th>
                    <th>Payment Mode</th>
                    <th>Amount</th>
                    <th>Actions</th>
                </tr>

                <TableData />
            </tbody>
            <OrderInfo />
        </table>
    )

    return (
        <div className="wrapper">
            <Header panelheader='My Bookings'>
                {tableData.length ? <TableComponent /> :
                    <h4 className='text-center'>
                        No Booking history found!
                    </h4>
                }
            </Header>
        </div>
    )
}

export default withRouter(BookingHistoryPage);
