import React from 'react';
import Map from './map';
import Category from './category';
import Myorder from './myorders';
import $ from 'jquery';
import { Route, Switch, Redirect, Link } from 'react-router-dom';
import Header from '../useradmin/header';
import Nav from '../useradmin/nav';
import { TextField, TextareaAutosize } from '@material-ui/core';
import { validateEmail, clear, validateFields, customValidation } from '../common.js'

const axios = require('axios');

class App1 extends React.Component {
  constructor(props) {
    super(props);
    this.errValid = null;
    this.timedout = null
    this.state = {
      count: 25,
      intervalId: 0,
      timer: '',
      username: '',
      mobno: '',
      email: '',
      otp: '',
      mobotp: '',
      error: { username: '', mobno: '', email: '', otp: '' },
      isError: false,
      isAccount: false,
      isRegister: false,
      isSubmit: false,
      isLogin: false,
      isOTP: false,
      isExist: false,
      btnHeader: '',
      tmobno: '',
      isResend: false,
      isRefresh: false,
      user: '',
      rememberMe: true
    }
    this.input = React.createRef();
    this.input1 = React.createRef();
    this.otp = React.createRef();
    this.name = React.createRef();
    this.emaill = React.createRef();
    this.clickType = this.clickType.bind(this);
    this.handleStartStop = this.handleStartStop.bind(this);
    this.login = React.createRef();
    this.remem = React.createRef();
  }
  handleStartStop () {
    let counter = setInterval(timer, 1000)
    this.state.count *= 60;
    function timer () {
      this.setState({
        count: this.state.count -= 1
      })
    }
  }

  onValidation = (cb) => {
    const emptyFields = [
      { key: 'username', errVal: 'Enter full name' },
      { key: 'mobno', errVal: 'Enter proper mobile number' }
    ]
    $("#error_email").hide();
    const data = validateFields(this.state, emptyFields)
    if (data.mobno !== '' && data.mobno.length !== 10)
      customValidation(data, 'mobno', 'Invalid mobile number')
    if (data.email !== '' && !validateEmail(data.email))
      customValidation(data, 'email', 'Invalid E-mail address')

    if (data.mobno.length === 10) {
      axios.get(global.serverURL + "/api/user/viewuser", { params: { "mobno": data.mobno } })
        .then((response) => {
          return response.data.data;
        }).then(data1 => {
          if (data1.length > 0) {
            customValidation(data, 'mobno', 'Mobile number already exist..! Choose sign in option..!')
            localStorage.setItem('bname', '');
            localStorage.setItem('bmobile', '');
            localStorage.setItem('bemail', '');
            this.setState({ isExist: true, data })
            return cb(data)
          }
          else {
            this.setState({ data })
            return cb(data)
          }
        })
    } else {
      this.setState({ data })
      return cb(data)
    }
  }

  onValidationLogin = (cb) => {
    const emptyFields = [
      { key: 'mobno', errVal: 'Enter proper mobile number' }
    ]
    const data = validateFields(this.state, emptyFields)
    if (data.mobno !== '' && data.mobno.length !== 10)
      customValidation(data, 'mobno', 'Invalid mobile number')

    if (data.mobno.length === 10) {
      axios.get(global.serverURL + "/api/user/viewuser", { params: { "mobno": data.mobno } })
        .then((response) => {
          return response.data.data;
        }).then(data1 => {
          if (data1.length > 0) {
            this.setState({ isExist: true, data })
            return cb(data)
          }
          else {
            customValidation(data, 'mobno', 'Mobile number doesnot exist..!')
            this.setState({ data })
            return cb(data)
          }
        })
    } else {
      this.setState({ data })
      return cb(data)
    }
  }

  componentDidMount () {
    var intervalId = setInterval(this.timer, 5000);
    // store intervalId in the state so it can be accessed later:
    this.setState({ intervalId: intervalId });

    const rememberMe = localStorage.getItem('rememberMe') === 'true';
    const user = localStorage.getItem('user');
    if (localStorage.getItem('bname') != undefined || localStorage.getItem('bname') != '') {
      $("#name").val(localStorage.getItem('bname'));
    }
    if (localStorage.getItem('bmobile') != undefined || localStorage.getItem('bmobile') != '') {
      $("#mobile").val(localStorage.getItem('bmobile'));
    }
    if (localStorage.getItem('bemail') != undefined || localStorage.getItem('bemail') != '') {
      $("#email").val(localStorage.getItem('bemail'));
    }
  }

  handleFormSubmit = () => {
    const { user, rememberMe } = this.state;
    localStorage.setItem('rememberMe', this.state.rememberMe);
    localStorage.setItem('user', this.state.mobno);
  };

  componentDidUpdate () {
    // var counter = document.getElementById("counter");
    // counter.innerHTML=""
    // if(!this.state.isSubmit)
    //   {if(this.state.isExist)
    //     {
    //      counter.innerHTML = "Please register your mobile number.!";
    //    }
    //    else
    //    {
    //      counter.innerHTML = "";
    //    }
    //  }
  }

  countdown = () => {
    var seconds = 60;
    var counter = document.getElementById("counter");
    clearTimeout(this.timedout)
    function tick () {
      seconds--;
      if (counter !== null) {
        counter.innerHTML = "0:" + (seconds < 10 ? "0" : "") + String(seconds);
        if (seconds > 0) {
          this.timedout = setTimeout(tick, 1000)
        } else {
          counter.innerHTML = "";
          //alert("Timed Out...You can try Resent Option..!!");
        }
      }
    }
    tick();
  }
  onChange1 = (event) => {
    this.setState({ [event.target.name]: event.target.value })
  }

  onChange = (e) => {
    this.setState({ [e.target.name]: e.target.value });
    if (e.target.name === 'mobno' && e.target.value.length === 10) {
      axios.get(global.serverURL + "/api/user/viewuser", {
        params: { "mobno": e.target.value }
      })
        .then((response) => {
          return response.data.data;
        })
        .then(userdata => {
          if (userdata.length) {
            localStorage.setItem('username', userdata[0].Name);
            $("#error_email").html("Mobile number already exist..!").css('text-align', 'left').show();
            const data = clear(this.state.error)
            this.setState({ data })
          }
          else {
            $("#error_email").hide();
          }
        }).catch(error => {
          console.error(error);
        });
    }
    else { $("#error_email").hide(); }

  }

  clickType = (type) => {
    if (type === 'submit') this.setState({ isSubmit: true });
    else {
      let resend = 'false'
      if (type === 'check' || type === 'register') {
        this.setState({ isRegister: false, isAccount: false });
      }
      if (type === 'resend') {
        this.setState({ isResend: true, isOTP: false });
        this.setState({ isRefresh: true });
        resend = 'true'
      }
      if (type === 'otp' && (this.state.isRegister || this.state.isOTP)) {
        const data = this.state
        if (this.state.otp === '') {
          customValidation(data, 'otp', 'Enter OTP')
        }
        else if (this.state.mobotp.toString() === this.state.otp.toString()) {
          clearTimeout(document.getElementById["counter"])
          this.handleFormSubmit();
          // this.setState({isSubmit:true}); 
          global.mobno = this.state.mobno
          this.props.history.go('/myorders')
        }
        else {
          customValidation(data, 'otp', 'Invalid OTP')
        }
        // this.setState({data})
      }
      else if (type === 'login') {
        this.setState({ isAccount: true });
      }
      else if (type === 'otp' || resend === 'true') {
        let mobno1 = this.state.mobno
        var counter = document.getElementById("counter");
        this.onValidationLogin(res => {
          if (!res.isError) {
            axios.post(global.serverURL + "/api/driver/checkmobno", {
              "mobno": mobno1, "role": "user", "type": "web"
            })
              .then((response) => {
                return response.data;
              })
              .then(data => {
                this.setState({ isOTP: true, isRegister: true, isLogin: true, mobotp: data.otp });
                this.countdown();
              }).catch(error => {
                console.error(error);
              });
          }
        })
      }
      else if (type === 'register') {
        let name1 = this.state.username
        let mobno1 = this.state.mobno
        let email1 = this.state.email

        this.onValidation(res => {
          if (!res.isError) {
            axios.post(global.serverURL + "/api/user/adduser", {
              "mobno": mobno1,
              "name": name1,
              "email": email1,
              "aadhar": "",
              "dob": "",
              "imeino": ""
            })
              .then((response) => {
                localStorage.setItem('username', name1)
                this.setState({ isRegister: true, isOTP: true, mobotp: response.data.otp });
                this.countdown();
              })
              .catch(error => {
                console.error(error);
              });
          }
        })
      }
    }
  }
  validateFields = (errname) => {
    this.errValid = '0'
    if (errname === 'mobno' && (this.state.mobno.trim() === '' || this.state.mobno.length < 10)) {
      this.errValid = '1';
      return false
    }
    return true
  }

  render () {
    const visibleFalse = {
      display: 'none'
    }

    const visibleTrue = {
      display: 'block',
      color: 'red',
      textAlign: 'right',
      fontSize: 'small',
      fontWeight: '400'
    }

    let RegisterBody;
    let LoginBody;
    let isCourier1 = this.state.isCourier;
    let isParcel1 = this.state.isParcel;
    const buttonstylefocus = {
      textDecoration: 'none',
      textAlign: 'center',
      display: 'inline-block',
      padding: '13px 20px',
      borderRadius: '10px',
      fontSize: '16px',
      letterSpacing: '1px',
      textTransform: 'uppercase',
      fontWeight: '700',
      border: '1px solid #f4511e',
      color: '#fff',
      background: '#f4511e'
    };
    const buttonstyle = {
      textDecoration: 'none',
      textAlign: 'center',
      display: 'inline-block',
      padding: '13px 20px',
      borderRadius: '10px',
      color: '#f4511e',
      fontSize: '16px',
      letterSpacing: '1px',
      textTransform: 'uppercase',
      border: '1px solid #f4511e',
      fontWeight: '700',
      background: 'transparent',
      boxShadow: '0 5px 10px 0 rgba(0, 0, 0, 0.2), 0 20px 29px 0 rgba(0, 0, 0, 0.19)'
    };
    const otp1 = this.state.isOTP ? {
      display: 'inherit'
    } : { display: 'none' }
    const otp2 = this.state.isOTP ? {
      display: 'none'
    } : { display: 'inherit' }

    RegisterBody = <div className="wrapper">

      <div className="">
        <div className="row flex-nowrap">
          <div className="col-md-6 col-xs-12 text-center">

            <div className="info-box" style={{ background: 'linear-gradient(-29deg, orange, violet)' }}>
              <span className="info-box-icon"><i className="fa fa-user-circle fa-2x"></i></span>

              <div className="info-box-content">
                <span className="info-box-text" style={{ fontSize: '26px', color: 'white' }}>{!this.state.isAccount ? 'Create an account' : 'Sign in'}</span>
                <div className="progress">
                  <div className="progress-bar" style={{ width: '100%' }}></div>
                </div>
                <span className="progress-description" style={{ color: 'white' }}>
                  It's quick and easy.
                </span>
              </div>
            </div>

            <div style={{ margin: '15px' }}>

              <TextField className="col-12 mb-2" type="text" name="username" onChange={this.onChange} id="standard-basic" label="Name" />
              {this.state.error.username}

              <div className='d-flex align-items-center'>
                <div>
                  <img src="https://img.icons8.com/color/28/000000/india.png" />
                </div>
                <div className='ml-2 w-100'>
                  <TextField className="col-12 mb-2" type="text" name="mobno" onChange={this.onChange} id="standard-basic" label="Mobile number" inputProps={{ maxLength: 10 }} value={this.state.mobno} />
                  {this.state.error.mobno}
                  <label id="error_email" style={{ display: 'contents', color: 'green', textAlign: 'left', float: 'left', fontSize: 'small' }}></label>
                </div>
              </div>


              <TextField className="col-12 mb-2" type="text" name="email" onChange={this.onChange} id="standard-basic" label="E-mail address (optional)" value={this.state.email} />
              {this.state.error.email}

              <span style={{ textAlign: 'right', float: 'right' }} id="counter"></span>

              <div className="row mt-5">
                <div className="col-12 mt-2">
                  <a href="#" style={this.state.isDomestic ? buttonstyle : buttonstylefocus} className="orange-back-link1 col-md-12 col-xs-12" onClick={() => this.clickType("register")}>Sign up</a>
                </div>

                <div className="col-12 mt-4">
                  <a href="#" className="col-12" style={{ textAlign: 'right', float: 'right' }} onClick={() => this.clickType('login')}>
                    <i className="fa fa-key"></i><span>  <u>Have an account? Sign in here.</u></span>
                  </a>
                </div>
              </div>

            </div>


          </div>

          <div className="col-md-6 col-xs-12 hidemobile" style={{ height: '100vh' }}>
            <img src="./dist/img/user slide screen - 4.jpg" width="100%" height="100%" alt="" />
          </div>

        </div>

      </div>

    </div>;


    LoginBody = <div className="wrapper">
      <div className="">
        <div className="row flex-nowrap">
          <div className="col-md-6 col-xs-12 text-center">

            <div className="info-box" style={{ background: 'linear-gradient(-29deg, orange, violet)' }}>
              <span className="info-box-icon"><i className="fa fa-user-circle fa-2x"></i></span>

              <div className="info-box-content">
                <span className="info-box-text" style={{ fontSize: '26px', color: 'white' }}>{this.state.isAccount ? 'Sign in' : 'Create an account'}</span>
                <div className="progress">
                  <div className="progress-bar" style={{ width: '100%' }}></div>
                </div>
                <span className="progress-description" style={{ color: 'white' }}>
                  It's quick and easy.
                </span>
              </div>
            </div>

            <div>
              <form action="" className="material form-group" style={{ margin: '15px', marginTop: '70px' }}>

                {(this.state.mobno === '' || !this.state.isRegister) ?
                  <div className='d-flex align-items-center'>
                    <div>
                      <img src="https://img.icons8.com/color/28/000000/india.png" />
                    </div>
                    <div className='ml-2 w-100'>
                      <TextField className="col-12 mb-2" type="text" name="mobno" onChange={this.onChange} id="standard-basic" label="Mobile number" inputProps={{ maxLength: 10 }} value={this.state.mobno} />
                    </div>
                  </div>
                  :
                  <div>
                    <div className='d-flex align-items-center'>
                      <div>
                        <img src="https://img.icons8.com/color/28/000000/india.png" />
                      </div>
                      <div className='ml-2 w-100'>
                        <TextField className="col-12 mb-2" type="text" name="mobno" onChange={this.onChange} id="standard-basic" label="Mobile number" inputProps={{ maxLength: 10, readOnly: true }} value={this.state.mobno} />
                      </div>
                    </div>
                    <TextField className="col-12 mb-2" type="text" name="otp" onChange={this.onChange} id="standard-basic" label="OTP" inputProps={{ maxLength: 6 }} value={this.state.otp} autoComplete="off" />
                    <span className="float-left">{this.state.error.otp}</span>
                    <span style={{ textAlign: 'right', float: 'right' }} id="counter"></span>
                    <div className="col-12 mt-4">
                      <a id="#resend" href="#" style={{ textAlign: 'left', float: 'left' }} onClick={() => this.clickType("resend")}>
                        <i className="fa fa-envelope-open"></i><span>  <u>Resend OTP?</u></span></a></div></div>
                }
                {this.state.error.mobno}

                <div className="row mt-5">
                  <div className="col-12 mt-2">
                    <a href="#" style={this.state.isDomestic ? buttonstyle : buttonstylefocus} className="orange-back-link1 col-md-12 col-xs-12 mb-4" onClick={() => this.clickType("otp")}> {this.state.isOTP ? 'Login here' : 'Generate OTP'} </a>
                    <a href="#" style={this.state.isDomestic ? buttonstyle : buttonstylefocus} className="orange-back-link1 col-md-12 col-xs-12" onClick={() => this.clickType("register")}> Create an account </a>
                  </div>

                </div>

              </form>

              <div style={otp1} className="row text-right">
                <div className="col-12" style={{ paddingRight: '1rem' }} >
                  <input ref={this.remem} type="checkbox" name="rememberMe" checked={this.state.rememberMe} onChange={() => { this.setState({ rememberMe: !this.state.rememberMe }) }} className="flat-red" />
                  <span>&nbsp;Remember me</span>

                </div>
              </div>

            </div>

          </div>

          <div className="col-md-6 col-xs-12 hidemobile" style={{ height: '100vh' }}>
            <img src="./dist/img/user slide screen - 4.jpg" width="100%" height="100%" alt="" />
          </div>

        </div>

      </div>

    </div>;

    if (isCourier1) {
      return <Map
        google={this.props.google}
        center={{ lat: 9.919136, lng: 78.118868 }}
        height='600px'
        zoom={15}
        isType='courier'
        mapHeader='Courier'
      />
    }
    else if (isParcel1) {
      return <Map
        google={this.props.google}
        center={{ lat: 9.919136, lng: 78.118868 }}
        height='600px'
        zoom={15}
        isType='parcel'
        mapHeader='Parcel'
      />
    }
    else if (this.state.isSubmit) {
      global.mobno = this.state.mobno
      return <Redirect to="/Booking/myorders" />
    }
    else if (!this.state.isRegister && !this.state.isAccount) return (RegisterBody);
    else return (LoginBody)
  }
}

export default App1;