/* eslint-disable max-len */
import React from 'react';
import Map from './map';
import Couriercalc from './couriercalc';
import Category from './category';
import Geocode from "react-geocode";
import $ from 'jquery';
import { Redirect, Link } from 'react-router-dom';
import { customValidation } from '../common.js'

const axios = require('axios');


class App1 extends React.Component {
  constructor(props) {
    super(props);
    this.errValid = '2'
    this.couponheader = ''
    this.changeAmount = this.changeAmount.bind(this);
    this.openCheckout = this.openCheckout.bind(this);
    this.state = {
      couponData: [],
      isRefresh: false,
      isSubmit: false,
      isOpen: false,
      isSubmit1: false,
      isBack: false,
      isNext: false,
      isSetup: false,
      isPersonal: false,
      isCOP: false,
      isCOD: false,
      isOnline: false,
      isCoupon: false, isApply: false,
      calcVal: {},
      toaddress: '',
      amount: 0,
      total: 0,
      clat: '', clng: '',
      damt1: '', damt2: '',
      bamt1: '', bamt2: '',
      samt: '', localdist: '',
      couponamt: '', gross: '',
      couponcode: '', personalno: '',
      error: { coupon: '' }
    }
    this.personal = React.createRef();
  }

  onChange = (event) => {
    this.setState({ [event.target.name]: event.target.value });
  }
  changeAmount (e) {
    this.setState({ amount: e.target.value })
  }

  openCheckout () {
    let options = {
      "key": "rzp_test_zfgh53To0jzwp9",
      "amount": this.state.amount, // 2000 paise = INR 20, amount in paisa
      "name": "Donkeycargo Pvt Ltd.,",
      "description": "Online Shipping System",
      "image": "./dist/img/DonkeyUser.png",
      "handler": function (response) {
        alert(response.razorpay_payment_id);
        this.setState({ isSubmit1: false });
        this.setState({ isSubmit: false });
        this.ratecalc();
      },
      "prefill": {
        "name": "Karthic",
        "email": "karthi@gmail.com"
      },
      "notes": {
        "address": "Hello World"
      },
      "theme": {
        "color": "#F37254"
      }
    };

    let rzp = new window.Razorpay(options);
    rzp.open();
  }

  validateFields = (errname) => {
    if (this.state.isApply) {
      this.errValid = '0'
      this.couponheader = ''
      if (this.state.couponcode === '') {
        this.errValid = '1';
        this.couponheader = 'Pick anyone coupon..!!';
        return true
      }
      else if (errname === 'coupon' && !this.state.isCoupon && !this.state.isRefresh) {
        this.errValid = '1';
        this.couponheader = 'Invalid coupon code..!!';
        return true
      }
      else if (errname === 'coupon' && !this.state.isCoupon && this.state.isRefresh) {
        this.errValid = '1';
        this.couponheader = 'The Coupon cannot be Used..!!';
        return true
      }
      else if (this.state.isCoupon) {
        this.couponheader = 'Coupon Applied Successfully..!!';
        return false
      }
    }
  }
  renderRedirect = (type) => {
    if (type === 'back')
      this.setState({ isBack: true, isNext: false });
    else
      this.props.history.push('/bookingconfirm')
  }
  clickType = (type, e) => {
    this.setState({ isSubmit: false });
    if (type === 'submit') {
      $("#cbtn").css("display", "none");
      $("#loading").css("display", "inline-block");
      let paymode1 = 'cash'
      let dd = axios.post(global.serverURL + "/api/courier/AddBooking", {
        "fromlat": this.props.fromlat,
        "fromlong": this.props.fromlng,
        "tolat": this.props.tolat,
        "tolong": this.props.tolng,
        "clat": this.state.clat,
        "clong": this.state.clng,
        "fromadd": this.props.address,
        "toadd": this.props.address1,
        "ctype": (this.props.isLocal) ? 'Local' : 'Domestic',
        "cname": this.props.courier,
        "product": (this.props.isLocal) ? this.props.dataLocal.product : this.props.dataDom.product,
        "localadd3": "",
        "localadd1": this.props.dataLocal.recadd1,
        "localadd2": this.props.dataLocal.recadd2,
        "dl1": this.props.dataDom.DL1,
        "db1": this.props.dataDom.DB1,
        "damt1": this.state.damt1,
        "dl2": this.props.dataDom.DL2,
        "db2": this.props.dataDom.DB2,
        "damt2": this.state.damt2,
        "bl1": this.props.dataDom.BL1,
        "bb1": this.props.dataDom.BB1,
        "bh1": this.props.dataDom.BH1,
        "bw1": "0",
        "bamt1": this.state.bamt1,
        "bl2": this.props.dataDom.BL2,
        "bb2": this.props.dataDom.BB2,
        "bh2": this.props.dataDom.BH2,
        "bw2": "0",
        "bamt2": this.state.bamt2,
        "total": this.state.gross,
        "paymode": paymode1,
        "couponcode": this.state.couponcode,
        "couponamt": this.state.couponamt,
        "nettotal": this.state.total,
        "iscancel": "0",
        "mobno": global.mobno,
        "bankrefno": "",
        "userid": "",
        "recname": this.props.dataLocal.recname,
        "recmobile": this.props.dataLocal.recmobno,
        "remarks": (this.props.isLocal) ? this.props.dataLocal.remarks : this.props.dataDom.remarks,
        "localdistance": this.state.localdist,
        "samt": this.state.samt,
        "personal": this.state.personalno,
        "noi": this.props.dataLocal.noi
      }).then((response) => {
        if (response.data === 'success') //||response.data==='driver not found'
        {
          //this.renderRedirect();
          this.setState({ isSubmit: true, isSubmit1: false });
          localStorage.removeItem('_order_edit')
        }
      }).catch(error => {
        console.error(error);
      });
    }
    else {
      if (type === 'coupon') {
        let data = this.state
        if (this.state.couponcode === '') {
          customValidation(data, 'coupon', 'Pick anyone coupon..!!')
          this.setState({ data })
        }
        else {
          customValidation(data, 'coupon', 'Coupon Applied Successfully..!!', 'success')
          this.setState({ isCoupon: true })
          this.ratecalc();
          // axios.get(global.serverURL + "/api/courier/getCoupon", {
          //   params: {
          //     "mobno": global.mobno, "couponcode": this.state.couponcode
          //   }
          // })
          //   .then((response) => {
          //     console.log(response)
          //     if (response.data.status === 'success') {
          //       let calc1 = this.state.gross - response.data.data[0].CouponAmt;
          //       if (calc1 < 0)
          //         customValidation(data, 'coupon', 'The Coupon cannot be used')
          //       else {
          //         customValidation(data, 'coupon', 'Coupon Applied Successfully..!!', 'success')
          //         this.setState({ isCoupon: true })
          //         this.ratecalc();
          //       }
          //     }
          //     else {
          //       customValidation(data, 'coupon', 'Invalid coupon code..!!')
          //     }
          //     this.setState({ data })

          //   }).catch(error => {
          //     console.error(error);
          //   });
        }
      }
      else if (type === 'couponconfirm') { this.ratecalc(); }
      else if (type === 'couponcancel') { this.setState({ couponcode: '', isCoupon: false,error: { coupon: '' } }); this.ratecalc('cancel'); }
      else if (type === 'cop') this.setState({ isCOP: true, isCOD: false, isOnline: false });
      else if (type === 'cod') this.setState({ isCOP: false, isCOD: true, isOnline: false });
      else if (type === 'online') this.setState({ isCOP: false, isCOD: false, isOnline: true });
      else if (type === 'setup') this.setState({ isSetup: true });
      else if (type === 'back')
        this.setState({ isBack: true, isNext: false });
      else if (type === 'next')
        this.setState({ isBack: false, isNext: true });
    }
  }
  static defaultProps = {
    address: 'Madurai East, Kamarajapuram Rd, Kamrajapuram, Tamil Nadu 625009, India',
    address1: 'Madurai East, Kamarajapuram Rd, Kamrajapuram, Tamil Nadu 625009, India',
    fromlat: '9.925523',
    fromlng: '78.340323',
    tolat: '9.905103',
    tolng: '78.103698',
    isLocal: true,
    dataDom: { courier: '', DL1: 0, DB1: 0, DL2: 0, DB2: 0, BL1: 0, BB1: 0, BH1: 0, BL2: 0, BB2: 0, BH2: 0 },
  };

  ratecalc (cond) {
    let fs = '', ts = '', couponcode1 = this.state.couponcode;
    if ((this.props.address).toLowerCase().includes('tamil'))
      fs = 'tamil'
    else if ((this.props.address).toLowerCase().includes('kera'))
      fs = 'kera'

    if ((this.props.address1).toLowerCase().includes('tamil'))
      ts = 'tamil'
    else if ((this.props.address1).toLowerCase().includes('kera'))
      ts = 'kera'
    if (cond === 'cancel')
      couponcode1 = ''
    axios.get(global.serverURL + "/api/courier/calc", {
      params: {
        "flat": this.props.fromlat, "flong": this.props.fromlng,
        "tlat": this.props.tolat, "tlong": this.props.tolng,
        "fromstate": fs, "tostate": ts,
        "couriertype": (this.props.isLocal) ? 'loc' : 'dom', "couriername": this.props.dataDom.courier,
        "DL1": this.props.dataDom.DL1, "DB1": this.props.dataDom.DB1, "DL2": this.props.dataDom.DL2, "DB2": this.props.dataDom.DB2,
        "BL1": this.props.dataDom.BL1, "BB1": this.props.dataDom.BB1, "BH1": this.props.dataDom.BH1, "BL2": this.props.dataDom.BL2, "BB2": this.props.dataDom.BB2, "BH2": this.props.dataDom.BH2,
        "couponcode": couponcode1, "city": "chennai"
      }
    })
      .then((response) => {
        return response.data.data[0];
      })
      .then(data => {
        this.setState({ calcVal: data });
        Geocode.fromLatLng(data.clatitude, data.clongitude).then(
          response => {
            const address = response.results[0].formatted_address;
            this.setState({ clat: data.clatitude, clng: data.clongitude, damt1: data.damt1, damt2: data.damt2, bamt1: data.bamt1, bamt2: data.bamt2, gross: data.gross, samt: data.samt, localdist: data.localdist, couponamt: data.disc, toaddress: address, amount: (data.Total * 100), total: data.Total });
          },
          error => {
            console.error(error);
          }
        );
      }).catch(error => {
        console.error(error);
      });
  }

  loadCoupons = async () => {
    try {
      const coupons = await axios.get(global.serverURL + `/api/courier/getUserCoupons?mobno=${global.mobno}`)
      this.setState({ couponData: coupons?.data?.data })
    } catch { }
  }
  componentDidMount () {
    this.ratecalc();
    this.loadCoupons()
  }

  render () {
    let res;
    let isCourier1 = this.state.isCourier;
    let isParcel1 = this.state.isParcel;
    const visibleFalse = {
      display: 'none'
    }

    const visibleTrue = {
      textDecoration: 'none',
      display: 'block',
      color: 'green',
      textAlign: 'right',
      fontSize: 'small',
      fontWeight: '400',
      padding: '5px'
    }

    const buttonstylefocus = {
      textDecoration: 'none',
      textAlign: 'center',
      display: 'inline-block',
      padding: '13px 20px',
      borderRadius: '25px',
      fontSize: '16px',
      letterSpacing: '1px',
      textTransform: 'uppercase',
      fontWeight: '700',
      border: '1px solid #f4511e',
      color: '#fff',
      background: '#f4511e',
      boxShadow: '0 5px 10px 0 rgba(0, 0, 0, 0.2), 0 20px 29px 0 rgba(0, 0, 0, 0.19)'
    };
    const buttonstyle = {
      textDecoration: 'none',
      textAlign: 'center',
      display: 'inline-block',
      padding: '13px 20px',
      borderRadius: '10px',
      color: '#f4511e',
      fontSize: '16px',
      letterSpacing: '1px',
      textTransform: 'uppercase',
      border: '1px solid #f4511e',
      fontWeight: '700',
      background: 'transparent'
    };

    const onCouponSelected = async (data) => {
      const couponData = [...this.state.couponData]
      couponData.forEach(item => {
        item.selected = false
        if (item.id === data.id) {
          item.selected = true
        }
      })
      this.setState({ couponData, couponcode: data.CouponCode })
    }

    const couponAlert = () => {
      const { couponData } = this.state
      return couponData?.length ? couponData?.map((data, index) => (
        <>
          <div className={`alert ${index % 2 === 0 ? 'alert-warning' : 'alert-success'}  alert-dismissible fade show`} role="alert" onClick={() => onCouponSelected(data)}>
            <strong>{data?.CouponCode}!</strong>
            <div>{data?.Description}</div>
            {
              data.selected && this.state.couponcode ? <button type="button" className="close">
                <span aria-hidden="true"><i class="fa fa-check" aria-hidden="true"></i></span>
              </button> : null
            }
          </div>
        </>
      )) : null
    }

    const goBookingHistory = () => {
      window.location.replace('/Booking/#/booking-history')
      localStorage.removeItem('_order_edit')
    }

    res = <div className="wrapper">
      <div className="">
        <div className="row flex-nowrap">
          <div className="col-md-6 col-xs-12 text-center">

            <div className="card card-success card-outline">
              <div className="card-header">

                <h3 className="card-title text-center">
                  <div className="row">
                    <div className="col-1">
                      {Boolean(localStorage.getItem('_order_edit')) ? null :
                        <div onClick={() => this.renderRedirect('back')}><i className="fa fa-arrow-left"></i></div>
                      }
                    </div>
                    <div className="col-10">
                      <span style={{
                        marginTop: '10px', fontFamily: 'fantacy', fontWeight: '500', fontSize: '28px',
                        background: 'linear-gradient(to right, darkviolet 0%, #f4511e 100%)',
                        WebkitBackgroundClip: 'text',
                        WebkitTextFillColor: 'transparent', marginLeft: '-10px'
                      }}> Payment Summary </span>
                    </div>
                    <div className="col-1">
                      {Boolean(localStorage.getItem('_order_edit')) ?
                        <div onClick={goBookingHistory} title='Booking history' className='cursor-pointer'><i className="fa fa-map-signs"></i></div>
                        : null}
                    </div>
                  </div>
                </h3>
              </div>

              <div className="card-body" style={{ padding: '0' }}>

                <div style={{ padding: '10px' }}>
                  <label className="select2text">Delivery Location</label>
                  <div className="input-group mb-3">
                    <div className="input-group-prepend">

                      <button type="button" className="btn btn-danger" style={{ width: "70px" }}>To</button>
                    </div>
                    <input type="text" className="form-control" onChange={this.onChange} disabled value={this.state.toaddress} />
                  </div>

                  <div className="row">

                    <div className="col-md-7 col-xs-7 mt-3" style={{ fontSize: '25px' }}>
                      <strong> Total Distance </strong>
                    </div>
                    <div className="col-md-5 col-xs-5 mt-3" style={{ fontSize: '30px' }}>
                      {Number(this.state.calcVal.localdist).toFixed('1')} Kms
                    </div>
                    <div className="col-md-7 col-xs-7 mt-3" style={{ fontSize: '25px' }}>
                      <strong> Gross Amount </strong>
                    </div>
                    <div className="col-md-5 col-xs-5 mt-3" style={{ fontSize: '30px' }}>
                      <i className="fa fa-inr fa-1x"></i><span>. <s>{this.state.gross}</s></span>
                    </div>
                    <div className="col-md-7 col-xs-7 mt-3" style={{ fontSize: '25px' }}>
                      <strong> Discount </strong>
                    </div>
                    <div className="col-md-5 col-xs-5 mt-3" style={{ fontSize: '30px' }}>
                      <i className="fa fa-inr fa-1x"></i><span>. {this.state.calcVal.disc}</span>
                    </div>
                    <div className="col-md-7 col-xs-7 mt-3" style={{ fontSize: '25px' }}>
                      <strong> Delivery Charges </strong> <i className="fa fa-info-circle" data-toggle="modal" data-target="#myModal" style={{ display: 'none' }}></i>
                    </div>
                    <div className="col-md-5 col-xs-5 mt-3" style={{ fontSize: '30px' }}>
                      <i className="fa fa-inr fa-1x"></i><span>. {this.state.calcVal.Total}</span>
                    </div>
                  </div>

                  <div className="row mt-5">
                    <div className="col-12 mt-2">
                      <a href="#" style={this.state.isDomestic ? buttonstylefocus : buttonstyle} className="orange-back-link col-md-12 col-xs-12" data-toggle="modal" data-target="#myModal3">Apply Coupon</a>
                    </div>
                  </div>

                  <div className="row rowflexdisable mt-2" style={{ paddingRight: '10px' }}>
                    <button id="cbtn" className="button col-12" onClick={this.state.isOnline ? this.openCheckout : (e) => this.clickType("submit", e)}><span>Continue</span></button>
                  </div>
                  <div id="loading" className="row" style={{ display: 'none' }}>
                    <img src="./dist/img/loading.gif" width="30" height="30" alt="" />
                  </div>

                </div>

              </div>

            </div>

          </div>

          <div className="col-md-6 col-xs-12 hidemobile" style={{ height: '100vh' }}>
            <img src="./dist/img/user slide screen - 4.jpg" width="100%" height="100%" alt="" />
          </div>

        </div>
      </div>

      <div id="myModal" className="modal fade" role="dialog">
        <div className="modal-dialog  modal-dialog-centered">

          <div className="modal-content">
            <div className="modal-header">
              <h4 className="modal-title">Payment Details</h4>
              <button type="button" className="close" data-dismiss="modal">&times;</button>
            </div>
            <div className="modal-body">
              <div className="row">
                <div className="col-6 mt-2">
                  {this.state.calcVal.docHeader}
                </div>
                <div className="col-6 mt-2">
                  <i className="fa fa-inr"></i><span>. {this.state.calcVal.docamt}</span>
                </div>
                <div className="col-6 mt-2">
                  {this.state.calcVal.boxHeader}
                </div>
                <div className="col-6 mt-2">
                  <i className="fa fa-inr"></i><span>. {this.state.calcVal.boxamt}</span>
                </div>
                <div className="col-6 mt-2">
                  Delivery Charges
                </div>
                <div className="col-6 mt-2">
                  <i className="fa fa-inr"></i><span>. {this.state.calcVal.samt}</span>
                </div>
                <div className="col-6 offset-6">
                  <hr />
                </div>
                <div className="col-6 mt-2">
                  Gross
                </div>
                <div className="col-6 mt-2">
                  <i className="fa fa-inr"></i><span>. {this.state.calcVal.gross}</span>
                </div>
                <div className="col-6 mt-2">
                  Disc
                </div>
                <div className="col-6 mt-2">
                  <i className="fa fa-inr"></i><span>. {this.state.calcVal.disc}</span>
                </div>
                <div className="col-6 offset-6">
                  <hr />
                </div>
                <div className="col-6">
                  Total
                </div>
                <div className="col-6">
                  <i className="fa fa-inr"></i><span>. {this.state.calcVal.Total}</span>
                </div>
                <div className="col-6 offset-6">
                  <hr />
                </div>
              </div>
            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
            </div>
          </div>

        </div>
      </div>

      <div id="myModal1" className="modal fade" role="dialog">
        <div className="modal-dialog  modal-dialog-centered">

          <div className="modal-content">
            <div className="modal-header">
              <h4 className="modal-title">Personal Mobile No.</h4>
              <button type="button" className="close" data-dismiss="modal">&times;</button>
            </div>
            <div className="modal-body">
              <div className="row">
                <input type="text" className="form-control" name="personalno" placeholder="Enter MobileNo" onChange={this.onChange} maxLength="10" />
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Ok</button>
                <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div ref={this.modal} id="myModal3" className="modal fade" role="dialog">
        <div className="modal-dialog  modal-dialog-centered">

          <div className="modal-content">
            <div className="modal-header">
              <h4 className="modal-title">Coupon Code</h4>
              <button type="button" className="close" data-dismiss="modal">&times;</button>
            </div>
            <div className="modal-body">
              <div className="row mx-2">
                <div className='p-0 mb-2'>Coupon picked</div>
                <input type="text" className="form-control" name="couponcode" placeholder="Coupon Code" onChange={this.onChange} value={this.state.couponcode}  disabled/>
                {this.state.error.coupon}
                <label className='p-0' style={!this.validateFields('coupon') ? visibleTrue : visibleTrue}>{this.couponheader}</label>
              </div>
              <div className='row mx-1'>
                {couponAlert()}
              </div>
              <div className="modal-footer">
                {!this.state.isCoupon ? <button type="button" data-dismiss={this.state.couponcode!==''?"modal":''} className="btn btn-default btn-primary" onClick={() => this.clickType("coupon")} >Apply Coupon</button> : <button type="button" className="btn btn-primary" data-dismiss="modal" onClick={() => this.clickType("couponconfirm")}>Ok</button>}
                <button type="button" className="btn btn-default btn-secondary" data-dismiss="modal" onClick={() => this.clickType("couponcancel")}>Cancel Coupon</button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div id="myModal2" className="modal fade" role="dialog">
        <div className="modal-dialog  modal-dialog-centered">

          <div className="modal-content">
            <div className="modal-header">
              <h4 className="modal-title">Setup Payment</h4>
              <button type="button" className="close" data-dismiss="modal">&times;</button>
            </div>
            <div className="modal-body">
              <div className="row">
                <div className="col-12">
                  <a href="#" style={this.state.isCOP ? buttonstylefocus : buttonstyle} className="orange-back-link col-12 mt-2 form-control" onClick={() => this.clickType("cop")} data-dismiss="modal">
                    <span style={{ display: 'grid' }}><i className="fa fa-truck fa-3x"></i>
                      Cash on Pickup</span></a>
                </div>

                <div className="col-12">
                  <a href="#" style={this.state.isCOD ? buttonstylefocus : buttonstyle} className="orange-back-link col-12 mt-2 form-control" onClick={() => this.clickType("cod")} data-dismiss="modal">
                    <span style={{ display: 'grid' }}><i className="fa fa-truck fa-flip-horizontal fa-3x"></i>
                      Cash on Delivery</span></a>
                </div>


              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
              </div>
            </div>

          </div>
        </div>
      </div>

    </div>;

    if (isCourier1) {
      return <Map
        google={this.props.google}
        center={{ lat: 9.919136, lng: 78.118868 }}
        height='600px'
        zoom={15}
        isType='courier'
        mapHeader='Courier'
      />
    }
    else if (isParcel1) {
      return <Map
        google={this.props.google}
        center={{ lat: 9.919136, lng: 78.118868 }}
        height='600px'
        zoom={15}
        isType='parcel'
        mapHeader='Parcel'
      />
    }
    else if (this.state.isBack) {
      return <Couriercalc address={this.props.address}
        address1={this.props.address1}
        fromlat={this.props.fromlat}
        fromlng={this.props.fromlng}
        tolat={this.props.tolat}
        tolng={this.props.tolng}
        isLocal={this.props.isLocal}
        isDomestic={this.props.isDomestic}
        dataLocal={this.props.dataLocal}
        dataDom={this.props.dataDom}
      />
    }
    else if (this.state.isNext) {
      return <Category />
    }
    else if (this.state.isSubmit && this.state.isSubmit1) {
      return <Redirect as={Link} to={{ pathname: "/" }} />
    }
    else if (this.state.isSubmit && !this.state.isSubmit1) {
      return <Redirect to={{ pathname: "/bookingconfirm" }} />
    }
    else
      return (res);
  }
}

export default App1;
